import Button from '@mui/material/Button';
import {createTheme,ThemeProvider } from '@mui/material/styles';
import AccountBalanceWalletOutlinedIcon from '@mui/icons-material/AccountBalanceWallet';
import {BtnTheme} from '../theme'

export default function Wallet(props) {
  return (
    <div style={{ position: "fixed", textAlign: "right", right: 0, top: 0, padding: 10 }}>
      <div style={{ display: "flex", flex: 1, alignItems: "center" }}>
        <ThemeProvider theme={BtnTheme}>
        <Button variant='outlined' startIcon={<AccountBalanceWalletOutlinedIcon/>} color='primary' onClick={() => {
          if (props.status === 'disconnect') {
            console.log('connect');
            props.connect();
          } else {
            console.log('disconnect');
            props.disconnect();
          }
        }}> 
          {props.text}
        </Button>
        </ThemeProvider>
      </div>
    </div>
  );
};


